<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5
    
    .titulo-principal
      .titulo-principal__numero
        span 4
      h1  Revisin de la gestin de condiciones de trabajo

    p.mb-5 Las condiciones de trabajo son aquellas que determinan las caractersticas del trabajo en relacin con la seguridad y salud en el trabajo, incluye salarios, locaciones, equipos, productos, instalaciones y tiles del centro de trabajo, aspectos ambientales, tecnolgicos y organizacionales de la empresa. Si ocurre una modificacin de alguno de estos tems se considera que ha habido una modificacin a las condiciones de trabajo.

    .row.justify-content-center.mb-5(data-aos="zoom-in")  
      .col-lg-10
        figure
          img(src='@/assets/curso/tema4/img01.png') 

    h2 Actualizacin de matriz de identificacin de peligros, evaluacin y valoracin de riesgos
    
    p.mb-4 Las empresas deben definir y aplicar una metodologa para la identificacin de peligros, evaluacin y valoracin de los riesgos de origen fsico, ergonmico o biomecnico, biolgico, qumico, de seguridad, pblico, psicosocial, entre otros, con alcance sobre todos los procesos, actividades rutinarias y no rutinarias, maquinaria y equipos en todos los centros de trabajo y respecto a todos los trabajadores, independientemente de su forma de vinculacin y/o contratacin.

    .row.mb-5
      .col-lg-8
        p Ahora bien, continuando con este aprendizaje, es importante dentro de cualquier empresa u organizacin la identificacin de peligros, evaluacin y valoracin de los riesgos, es una actividad enfocada en analizar los procesos, las actividades rutinarias y no rutinarias, el uso de maquinaria y equipo, el estado de las instalaciones, las condiciones laborales de los trabajadores, cuyo resultado es la implementacin de medidas de prevencin y control necesarias, segn la Resolucin 0312 del 2019 emitida por el Ministerio del Trabajo. Esta identificacin debe realizarse con la participacin de los trabajadores de todos los niveles de la empresa y actualizarla como mnimo una vez al ao y cada vez que ocurra un accidente de trabajo mortal, un evento catastrfico en la empresa o cuando se presenten cambios en los procesos, en las instalaciones, en la maquinaria y equipos o en las condiciones ambientales.
      .col-lg-4
        figure
          img(src='@/assets/curso/tema4/img02.svg')

    .row.justify-content-center.mb-5
      .col-lg-10
        .bloque-texto-f.color-secundario.div2.pt-4.px-5.mb-5
          .bloque-texto-f__comillas.div2
            i.fas.fa-quote-left
            i.fas.fa-quote-right
          p.text-regular.bloque-texto-f__texto Se debe definir y aplicar una metodologa para la identificacin de peligros, evaluacin y valoracin de los riesgos de origen fsico, ergonmico o biomecnico, biolgico, qumico, de seguridad, pblico, psicosocial, entre otros, e identificar con base en la valoracin de los riesgos, aquellos que son prioritarios, esta metodologa debe tener alcance a todos los procesos, actividades rutinarias y no rutinarias, maquinaria y equipos en todos los centros de trabajo y respecto a todos los trabajadores, independientemente de su forma de vinculacin y/o contratacin.

    div.pb-5.mb-5(style="background-color: #FEF6E1")(data-aos="zoom-out")      
      .row.justify-content-center
        .col-lg-6
          figure(style="position:relative; top:-55%")
            img(src='@/assets/curso/tema4/img03.svg')
      p.text-center <b>Existen varias metodologas para la identificacin de peligros, evaluacin y valoracin de los riesgos, entre las que se encuentran:</b>
      .row.justify-content-center
        .col-lg-6.ps-5.pe-5(style="border-right:3px dotted #B9B2A1")
          p.mb-4 La metodologa de la Gua Tcnica Colombiana GTC-45 de 2012 es emitida por el ICONTEC. Esta gua es un instrumento tcnico diseado para la identificacin de peligros, la evaluacin y valoracin de los riesgos, muestra definiciones y conceptos como: accidentes de trabajo, las actividades rutinarias y no rutinarias, anlisis de riesgo, diagnstico de condiciones de salud, elementos de proteccin personal, enfermedad profesional, evaluacin de riesgos, identificacin de peligros, entre otros.
        .col-lg-6.ps-5.pe-5
          p.mb-4 Gua Tcnica de Implementacin del SG-SST para mipymes del Ministerio de Trabajo, para el efecto se debe tener en cuenta la legislacin vigente, los cambios de los procesos, procedimientos, personal, instalaciones y asegurar la inclusin de todas las actividades rutinarias y no rutinarias de las empresas. En caso de que la empresa sea una mipyme (micro, mediana o pequea empresa) y no presente actividades de alto riesgo.

    p.text-center <b>Los pasos generales para la actualizacin de la matriz de identificacin de peligros y la valoracin de los riesgos de la empresa son los siguientes:</b>

    .row.justify-content-center.mb-5
      .col-lg-10
        PasosA.color-secundario.mb-5(tipo="l")
          p(data-aos="flip-right")   Identificar cada uno de los procesos, actividades y tareas que se realicen (rutinarios y no rutinarios), clasificar los peligros ocupacionales, identificar los controles existentes y su nivel de efectividad.
          p(data-aos="flip-left")  Valorar el riesgo a travs de la calificacin del riesgo asociado a cada peligro (a mayor probabilidad de que suceda y mayor severidad de las consecuencias, mayor es el nivel de riesgo). Si en las actividades de la empresa se identifican tareas de alto riesgo, estas se deben identificar y relacionar en el SG-SST con los nombres de los trabajadores dedicados a ellas de forma permanente.
          div(data-aos="flip-right") 
            p.mb-4 <b>Medidas de prevencin y control frente a peligros/riesgos identificados:</b>
            p.mb-4 Se establecen las medidas de prevencin y control con base en el resultado de la identificacin de peligros, la evaluacin y valoracin de los riesgos (fsicos, ergonmicos, biolgicos, qumicos, de seguridad, pblicos, psicosociales, entre otros), que incluya los prioritarios y adems se debe verificar que se ejecuten acorde con el esquema de jerarquizacin, de ser factible priorizar la intervencin en la fuente y en el medio.
            p.mb-4 Si se requieren controles nuevos o mejorados, estos deben ser priorizados y luego clasificados de acuerdo con el principio de eliminacin de peligros, y orientados hacia la reduccin de riesgos, con el fin de reducir la probabilidad de ocurrencia o la severidad potencial de las lesiones o daos, estos pueden ser: 

            ol.lista-ol.ms-5
              li 
                span.text-bold(style="color:#71B0BB") a) 
                | De eliminacin.
              li 
                span.text-bold(style="color:#71B0BB") b) 
                | De sustitucin.
              li 
                span.text-bold(style="color:#71B0BB") c) 
                | De ingeniera. 
              li 
                span.text-bold(style="color:#71B0BB") d) 
                | De sealizacin / advertencias y/o controles administrativos. 
              li 
                span.text-bold(style="color:#71B0BB") e) 
                | De equipos de proteccin personal.
          p(data-aos="flip-left")  Elaborar plan de accin que contenga las medidas de prevencin y control para intervenir los peligros/riesgos, lo cual debe quedar documentado. 
          p(data-aos="flip-right")  Mantener, actualizar y mejorar la matriz debe ser un proceso constante y de seguimiento continuo.
          p(data-aos="flip-left")  Evaluacin y valoracin de los riesgos, corresponde al proceso de determinar la probabilidad de que ocurran eventos especficos y la magnitud de sus consecuencias, mediante el uso sistemtico de la informacin disponible. 

    p.mb-5 La evaluacin de los riesgos corresponde al proceso de determinar la probabilidad de que ocurran eventos especficos y la magnitud de sus consecuencias, mediante el uso sistemtico de la informacin disponible.

    p.mb-5.text-center <b>Para evaluar el nivel de riesgo (NR) se debe determinar lo siguiente: </b>

    .row.mb-4.justify-content-center(data-aos="fade-in") 
      .col-lg-10
        figure.mb-5
          img(src='@/assets/curso/tema4/img04.png')
          figcaption Nota. Tomado de http://132.255.23.82/sipnvo/normatividad/GTC_45_DE_2012.pdf

        p Lo importante es determinar el nivel de riesgo, segn el ICONTEC la magnitud del riesgo resultante del producto del nivel de probabilidad por el nivel de consecuencia (ICONTEC, 2012). En otras palabras, es la combinacin de la probabilidad de que el peligro se materialice.
    
    .row.mb-5.justify-content-center
      .col-lg-8
        a.anexo.mb-4(:href="obtenerLink('/downloads/Valoracion_Riesgos.pdf')" target="_blank")
          .anexo__icono
            img(src="@/assets/template/icono-pdf.svg")
          .anexo__texto
            p <b>Anexo.</b> Estimado aprendiz para mejorar la comprensin de este tema descargue el documento denominado <b>Valoracin riesgos.</b>

    .row.mb-5.justify-content-center
      .col-lg-8
        p.mb-4 Una vez determinado el nivel de riesgo, la organizacin debe decidir cules riesgos son aceptables y cules no.
        p.mb-4 Mediante una evaluacin completamente cuantitativa es posible evaluar el riesgo antes de decidir el nivel que se considera aceptable o no; sin embargo, con mtodos semicuantitativos tales como la matriz de riesgos, la organizacin debe establecer cules categoras son aceptables y cules no.
        p.mb-4 Para el efecto, la organizacin inicialmente debe establecer los criterios de aceptabilidad, con el fin de proporcionar una base que brinde consistencia en todas sus valoraciones de riesgos, debe incluir la consulta a las partes interesadas y tener en cuenta la legislacin vigente.
        p.mb-4 A continuacin, como se muestra en la siguiente tabla, podr observar como ejemplo la clasificacin de aceptabilidad del riesgo.
      .col-lg-4
        figure.mb-5
          img(src='@/assets/curso/tema4/img05.png')

    .row.mb-5.justify-content-center
      .col-lg-3.px-5.d-flex.justify-content-end
        div
          figure.mb-5
            img(src='@/assets/curso/tema4/img06.svg')
      .col-lg-8(data-aos="zoom-out") 
        div.BorTit.mb-3
          p.mb-0 <b>Tabla 1</b>
          p.mb-0 Aceptabilidad del riesgo

