            p Para conocer ms detalladamente sobre la matriz de peligros consulte la Gua Tcnica Colombiana 45 del 2012.

    p.text-center.mb-5 <b>A continuacin, se expondrn los principales peligros a los que pueden estar expuestos los trabajadores dentro de su jornada laboral.</b>

    AcordionA.mb-5(tipo="b" clase-tarjeta="tarjeta tarjeta--azul acordeon")    
        div(numero="1" titulo="1. Peligro biolgico")
          .row.justify-content-center.mb-5
            .col-lg-3
              figure
                img(src='@/assets/curso/tema4/img07.svg')
            .col-lg-9
              p.mb-4 Surge por la exposicin laboral a micro y macroorganismos que puedan causar daos al trabajador. Comprende enfermedades transmitidas por animales, plantas o vectores, agentes que transportan y transmiten patgenos. Su transmisin puede ser por va respiratoria, digestiva, sangunea, piel o mucosas y pueden ser virus, bacterias, hongos, rickettsias, parsitos, picaduras, mordeduras. Los efectos posibles en el organismo son el desarrollo de enfermedades infectocontagiosas como COVID 19, hepatitis, SIDA, herpes, tuberculosis, zoonosis, carbunco, leptospirosis, fiebre tifoidea, ttanos, varicela, infecciones de varios tipos, entre otras. 
              p El riesgo biolgico ocupacional hace parte del riesgo biolgico, es aquel que surge de la exposicin a micro y macroorganismos que puedan causar daos al trabajador, y pueden ser transmitidos a travs del aire, de la sangre y de los fluidos corporales; en las instituciones de salud es uno de los factores de riesgo ms importantes, su exposicin  se manifiesta de forma directa o indirecta. 
          
          div.div-blanco.p-5.mb-5
            p.mb-4 <b>Directa:</b> se origina cuando el personal especialmente el de laboratorio manipula directamente los microorganismos a travs de las tcnicas o procedimientos que pudiesen liberar al medio ambiente cierta cantidad de estos, ya sea por la ejecucin de tales procedimientos o por la ocurrencia de algn accidente, tambin por la disposicin de los desechos tratados inadecuadamente. 
            p <b>Indirecta:</b> se deriva de la atencin de la fuente contaminante que puede ser un ser vivo o muerto o por las secreciones emanadas por ellos o por un elemento que fue utilizado para su atencin o manipulacin.
            figure.px-5
              img(src='@/assets/curso/tema4/img08.svg')
          
          .row.justify-content-center.mb-5
            .col-lg-8
              a.anexo.mb-4.div-blanco(:href="obtenerLink('/downloads/GTHS02.pdf')" target="_blank")
                .anexo__icono
                  img(src="@/assets/template/icono-pdf.svg")
                .anexo__texto
                  p.mb-0 <b>Anexo. Riesgo biolgico.</b> Elementos de proteccin personal para riesgo biolgico.
                  
          h3.mb-5 Controles propuestos para gestionar el peligro biolgico 

          h4.titulo-naranja Controles de ingeniera
          p.mb-5 Disear sistemas de ventilacin en los sitios de trabajo, diseo de dispensadores de jabn y llaves de agua que eviten el contacto directo, sealizacin y advertencias.
          figure.px-3.mb-5
              img(src='@/assets/curso/tema4/img09.png')

          h4.titulo-naranja Controles administrativos
          .row.justify-content-center
            .col-lg-8
              ul.lista-ul--color.t1.ms-5
                li
                  i.fas.fa-check-double
                  | Sealizacin visible de uso obligatorio de tapabocas, distanciamiento fsico, lavado de manos, establecer un programa de vigilancia epidemiolgico, que contenga exmenes mdicos ocupacionales con sus respectivos diagnsticos de las condiciones de salud.
                li
                  i.fas.fa-check-double
                  | Capacitacin e implementacin en normas universales de bioseguridad.
                li
                  i.fas.fa-check-double
                  | Capacitacin en lavado de manos.
                li
                  i.fas.fa-check-double
                  | Base de datos de vacunas (recordar Circular 0022 de 2021).
                li
                  i.fas.fa-check-double
                  | Indicadores de prevalencia e incidencia de enfermedad por riesgo biolgico.
                li
                  i.fas.fa-check-double
                  | Implementar programas de limpieza y desinfeccin de reas. 
                li
                  i.fas.fa-check-double
                  | Implementar programa de manejo de residuos.
                li
                  i.fas.fa-check-double
                  | Implementar programa de inspecciones generales y de elementos de proteccin personal: tapabocas quirrgico.
                li
                  i.fas.fa-check-double
                  | Capacitar en riesgo biolgico.
            .col-lg-4
              figure
               img(src='@/assets/curso/tema4/img10.svg')
          

        div(numero="2" titulo="2. Peligro psicosocial")
          .row.mb-5
            .col-lg-3
              figure.px-4
               img(src='@/assets/curso/tema4/img11.svg')
            .col-lg-9
              p.mb-4 De acuerdo con la Organizacin Internacional del Trabajo (OIT) los riesgos psicosociales son aquellas caractersticas de las condiciones de trabajo que afectan  la salud de las personas a travs de mecanismos psicolgicos y fisiolgicos, produciendo lo que se llama estrs. Se puede  entender mejor la palabra psicosociales si se descompone de la siguiente manera: psico porque  afectan a travs de la psique (conjunto de actos y funciones de la mente) y social porque su origen es social (determinadas caractersticas de la organizacin del trabajo).
              p El estrs laboral relacionado al trabajo est determinado por la manera en que est organizado el ambiente de trabajo, por su diseo y por las relaciones laborales; se presenta cuando las exigencias del trabajo exceden las capacidades, recursos o necesidades del trabajador; tambin cuando el conocimiento y habilidades de un trabajador o un grupo de colaboradores no cumple con las expectativas de una empresa.

          .cajon.color-acento-botones.p-4.mb-4        
            p Cuando se ejecutan labores en ambientes en el que el bienestar general se ve afectado se puede decir que se est en presencia de riesgos psicosociales. De igual manera, se est relacionado con las caractersticas de la persona como son hbitos, conductas inseguras, caractersticas biolgicas y orgnicas, autoestima, personalidad, entre otras.

          p.mb-4 En el contexto de la pandemia, por ejemplo, en donde las condiciones de vida y de trabajo se vieron alteradas, dado que las familias viven y trabajan en el mismo sitio, se realizan actividades domsticas al mismo tiempo en que se ayuda a los hijos con las clases virtuales, la seal de Internet a veces no funciona bien, entre otras, han hecho que el ritmo de vida y la realidad que se vive altere la estabilidad de las familias y en este contexto el ambiente de trabajo. 
          p.mb-5 Por lo anterior, el Gobierno Nacional ha emitido la normatividad para el trabajo en casa, esto con el fin de controlar el peligro psicosocial apoyndose en las Administradoras de Riesgos Laborales, quienes tienen la responsabilidad de realizar actividades de promocin y prevencin a los trabajadores de las empresas afiliadas.

          .row.justify-content-center.mb-5
            .col-lg-8
              figure
                img(src='@/assets/curso/tema4/img12.svg')

          div.div-blanco.p-5.mb-5
            p.mb-5.text-center <b>De acuerdo con la Gua Tcnica 45 de 2012 los peligros psicosociales estn relacionados con:</b>
            .row
              .col-lg-6
                ul.lista-ul--color.t1.ms-5
                  li
                    i.fas.fa-check-double
                    | La gestin organizacional.
                  li
                    i.fas.fa-check-double
                    | Las caractersticas de la organizacin del trabajo.
                  li
                    i.fas.fa-check-double
                    | Las caractersticas del grupo social de trabajo.
                  li
                    i.fas.fa-check-double
                    | Las condiciones de la tarea.
                  li
                    i.fas.fa-check-double
                    | La interfaz persona  tarea.
                  li
                    i.fas.fa-check-double
                    | La jornada de trabajo.
              .col-lg-6
                figure.px-4
                  img(src='@/assets/curso/tema4/img13.svg')
      
          p.mb-5 Los controles propuestos para el peligro psicosocial que estn basados en la legislacin vigente en la que se contempla el trabajo en casa, regulado en la Circular 041 y la Ley 2088 de 2021 son los siguientes:

          h4.titulo-naranja Controles administrativos

          .row
            .col-lg-8
              ul.lista-ul--color.t1.ms-5
                li
                  i.fas.fa-check-double
                  | Establecer a todos los trabajadores y empleadores un horario y jornada de trabajo, con el fin de garantizar el derecho a la desconexin y evitar impactos que se puedan generar en la salud mental y el equilibrio emocional de los trabajadores. 
                li
                  i.fas.fa-check-double
                  | Pago de horas extras, recargos dominicales y festivos si aplica.
                li
                  i.fas.fa-check-double
                  | Permanencia de carga habitual de trabajo.
                li
                  i.fas.fa-check-double
                  | Espacio para pausas activas de higiene y de proteccin de la salud.
                li
                  i.fas.fa-check-double
                  | Horas de trabajo divididas en dos secciones. 
                li
                  i.fas.fa-check-double
                  | Programas de bienestar y capacitacin que debe implementar el empleador y la ARL.
                li
                  i.fas.fa-check-double
                  | El comit de convivencia laboral debe contar con los mecanismos que faciliten a los trabajadores el reporte de quejas. 
                li
                  i.fas.fa-check-double
                  | La ARL incluir el trabajo en casa dentro de sus actividades de promocin y prevencin y, apoyar la gestin de pausas activas.
            .col-lg-4
              figure.px-3
                  img(src='@/assets/curso/tema4/img14.svg')

        div(numero="3" titulo="3. Peligro biomecnico")  
          .row.mb-5
            .col-lg-4
              figure.px-4
                  img(src='@/assets/curso/tema4/img15.svg')
            .col-lg-8
              p.mb-4 Son aquellos peligros inherentes al proceso o tarea, que incluyan aspectos organizacionales en la interaccin hombre  medio ambiente, condiciones de trabajo y productividad y, que estn relacionados con las posturas, movimientos inadecuados, sobresfuerzo, movimiento repetitivo, con el potencial de producir fatiga fsica y dao en el msculo esqueltico. 
              p Est presente en todas las actividades que se cumplen dentro de una empresa, ya que siempre se requiere de una postura, ya sea sentada, de pie, semisentado o acostado, lo que puede traer consigo fatiga muscular, cansancio y sobreuso de articulaciones, ocasionando eventualmente lesiones osteomusculares, las cuales son consideradas como una de las principales enfermedades laborales en Colombia.

          div.div-blanco.p-5.mb-5
            h4.titulo-naranja Controles administrativos
            p.mb-5 En el marco de la pandemia y de acuerdo con la Resolucin 777 de 2021 se establece que la ARL debe incluir dentro de sus actividades de promocin y prevencin las pausas activas en el trabajo en casa, por lo que la empresa debe gestionar con la ARL las pausas activas dentro de sus actividades de promocin y prevencin y, el apoyo a la gestin del trabajo en casa.
            figure.px-5
                  img(src='@/assets/curso/tema4/img16.svg')

        div(numero="4" titulo="4. Peligro qumico")  
          .row.mb-5          
            .col-lg-5
              p Producido por contacto e inhalacin de sustancias qumicas como polvos, gases, vapores, fibras, lquidos (nieblas y rocos), slidos, material particulado, humos metlicos y no metlicos, las cuales puede producir efectos indeseados en el trabajador, que pueden ser agudos o crnicos y/o provocar la aparicin de enfermedades. Sus efectos dependen de la naturaleza del producto, la va de penetracin, el tiempo de exposicin y la predisposicin del trabajador, entre otras.
            .col-lg-7
              figure.px-4
                  img(src='@/assets/curso/tema4/img17.svg')
          div.div-blanco.p-5.mb-5
            .row
              .col-lg-6
                h4.titulo-naranja Controles de ingeniera
                p.mb-4 Almacenar las sustancias qumicas, de acuerdo con la matriz de compatibilidad de sustancias qumicas.

                h4.titulo-naranja Controles administrativos
                ul.lista-ul--color.t1.ms-4
                  li
                    i.fas.fa-check-double
                    | Capacitar en el uso de manipulacin de sustancias qumicas utilizadas para limpieza y desinfeccin de reas, as tambin en las especificaciones de dosis y naturaleza qumica del producto y elementos de proteccin personal.
                  li
                    i.fas.fa-check-double
                    | Mantener las hojas de seguridad de acuerdo con lo que se indica en la Resolucin 773 de 2021 o Sistema Globalmente Armonizado  SGA.
                  li
                    i.fas.fa-check-double
                    | Sealizacin de acuerdo con el Sistema Globalmente Armonizado  SGA.
                    
              .col-lg-6
                figure
                  img(src='@/assets/curso/tema4/img18.svg')

        div(numero="5" titulo="5. Peligro fsico")     
          p.mb-5 Son aquellas situaciones del ambiente de naturaleza fsica que pueden provocar efectos dainos a la salud de los trabajadores de acuerdo con la concentracin, tiempo de exposicin e intensidad del contaminante, de estos peligros se pueden realizar mediciones ambientales, dentro de los que se encuentran: 

          div.div-blanco.p-5.mb-5
